//import Media from '../../CommonComponents/Media/Media';
import PostMedia from './common/postMedia';
import ShowMoreContent from './common/showMoreContent';



const SliderItem = ({ assetItem, assetKey, active, newPicture }) => {
    return active ? <>
    {/* <Media key={assetKey} mediaType={'assetItem.type'} postFile={assetItem.postFile} mediaHeight={500} mediaWidth={500} socialNetwork={'assetItem.type==1?assetItem.networkId'} textContent={''} rating={''} playVideo={true} mediaFile={'assetItem.mediaFile'} /> */}

        <div className="py-3 w-100">
            <div className="d-flex algin-items-center justify-content-between w-100 mb-2">
                <div className="d-flex align-items-center flex-grow-1 w-100 overflow-hidden cursor-pointer" onClick={event => window.open(assetItem.link, "blank")}>

                    <PostMedia multiPost={1}  postFile={assetItem.postFile} postId={assetItem.postId} className={"img-fluid"} />
                    <div className="pe-2 w-100 overflow-hidden">
                        <div className="fs-10 d-flex align-items-center text-gray-500">
                            <p className="mb-0 text-ellipsis">@{assetItem.username}</p>
                            <i className="ri-checkbox-blank-circle-fill mx-1" style={{ fontSize: 5 }}></i>
                            {/* <span className="mb-0 text-ellipsis" style={{ minWidth: 100 }}>{moment(new Date(assetItem.createdAt * 1000)).fromNow()}</span> */}
                        </div>
                    </div>
                </div>
                {/* <span className="d-flex">
                    <img className="img-fluid" src={baseUrl(`media/icons/social-icons/${GET_NETWORK_ICON(assetItem.networkId)}.svg`)} height={22} width={22} alt={GET_NETWORK_NAME(assetItem.networkId)} />
                </span> */}
            </div>
            <ShowMoreContent classes={'influ_content'} content={assetItem.content} idIndex={`influ-${assetKey}`} line={3} />
        </div></> : null
}

export default SliderItem;